.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  line-height: 14px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  width: 40px;
  height: 40px;
}

.nav .nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav .nav__list .nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav .nav__list .nav__link {
  color: #060b35;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav .nav__list .nav__link.is-active {
  color: #00acdc;
  position: relative;
}

.nav .nav__list .nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.nav .nav__list .nav__link:hover {
  color: #00acdc;
}

.product {
  box-sizing: border-box;
  cursor: pointer;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  flex-shrink: 0;
  justify-content: space-between;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s linear;
  display: flex;
}

.product:hover {
  transform: scale(1.2);
}

.product__img {
  align-self: center;
  width: 160px;
  height: 134px;
  margin: 0 auto;
  padding-bottom: 40px;
  display: block;
}

.product__title {
  color: #060b35;
  margin: 0;
  padding: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product__code {
  color: #616070;
  padding-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.product__rating {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 24px;
  display: flex;
}

.product__rating .product__reviews {
  text-align: right;
  color: #060b35;
  font-size: 10px;
  line-height: 14px;
}

.product .price {
  justify-content: space-between;
  padding-bottom: 16px;
  display: flex;
}

.product .price__title, .product .price__subtitle {
  margin: 0;
  line-height: 18px;
  display: block;
}

.product .price__title {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.product .price__subtitle {
  color: #616070;
  font-size: 12px;
}

.product__button {
  text-transform: uppercase;
  color: #fff;
  background: #00acdc;
  border: none;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-family: Roboto, Helvetica, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
}

.product:hover > .product__button {
  color: #00acdc;
  background: #fff;
  border: 1px solid #00acdc;
}

.stars {
  gap: 4px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars--0 .stars__star:nth-child(-n), .stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(n) {
  background-image: url("star-active.edda4c43.svg");
}

html {
  font-family: Roboto, Helvetica, sans-serif;
}

body {
  margin: 0 auto;
}

.main {
  grid-template-columns: repeat(4, auto);
  justify-content: center;
  place-items: center;
  gap: 46px 48px;
  padding: 50px 40px 72px;
  display: grid;
}

@media (width <= 487px) {
  .main {
    grid-template-columns: 1fr;
  }
}

@media (width >= 488px) and (width <= 767px) {
  .main {
    grid-template-columns: repeat(2, auto);
  }
}

@media (width >= 768px) and (width <= 1023px) {
  .main {
    grid-template-columns: repeat(3, auto);
  }
}

